<template>
  <div class="flex flex-col gap-4 lg:gap-8">
    <p class="font-medium text-3xl">
      女教師玩具化計畫女教師玩具化計畫女教師玩具化計畫女教師玩具化計畫女教師玩化
    </p>
    <div class="lg:px-10">
      <div class="w-full lg:h-540 h-80 bg-black rounded-md"></div>
    </div>
    <div class="flex justify-between items-center flex-col lg:flex-row">
      <p>
        {{ $t("click_of_num") }}：552 | {{ $t("listed") }}：2021-11-10 |
        {{ $t("upload_by") }}：nnisfjjor
      </p>
      <DetailOperation />
    </div>
    <DetailTags></DetailTags>
    <MessageBoard />
    <div class="hidden lg:block flex-shrink-0 mt-10 font-medium">
      <p class="text-2xl mb-6">{{ $t("you_may_like") }}</p>
      <div class="grid grid-cols-4 gap-5">
        <Card v-for="item in dataSource" :dataSource="item" :key="item.id" />
      </div>
    </div>
    <div class="block lg:hidden flex-shrink-0 mt-10 font-medium">
      <p class="text-2xl mb-6">{{ $t("you_may_like") }}</p>
      <div class="grid grid-cols-1 w-full gap-y-5">
        <template v-for="item in dataSource">
          <div
            :key="item.id"
            v-if="item.type === 'ad'"
            class="h-48 bg-gray-100 flex items-center justify-center col-span-2"
          >
            ad
          </div>
          <Card :key="item.id" :dataSource="item" v-else></Card>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import MessageBoard from "@/components/MessageBoard";
import DetailOperation from "@/components/DetailOperation";
import DetailTags from "@/components/DetailTags";
import Card from "@/components/Card";
import mobileMixin from "../../mixin/mobileMixin";
import { videos } from "../../fakeData";

const fakeVideo = videos.slice(0, 4).map((item) => ({
  ...item,
  path: "/video",
}));
const mobileFakeVideo = videos
  .slice(0, 4)
  .map((item) => ({
    ...item,
    path: "/video",
  }))
  .reduce(
    (acc, item, index) => {
      if ((index + 1) % 1 === 0) {
        return [
          ...acc,
          item,
          {
            type: "ad",
          },
        ];
      }
      return [...acc, item];
    },
    [
      {
        type: "ad",
      },
    ]
  );
export default {
  mixins: [mobileMixin],
  components: {
    MessageBoard,
    DetailOperation,
    DetailTags,
    Card,
  },
  data() {
    return {
      dataSource: fakeVideo,
    };
  },
  methods: {
    handleAddAds() {
      this.dataSource = mobileFakeVideo;
    },
    handleRemoveAds() {
      this.dataSource = fakeVideo;
    },
  },
  watch: {
    isMobile: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue && !oldValue) {
          this.handleAddAds();
        } else {
          this.handleRemoveAds();
        }
      },
    },
  },
};
</script>
